<template>
    <div id="appPerson">
        <img src="https://lh3.googleusercontent.com/proxy/-yr748w69iSMgzU60rIixYKO8Bi1tf0PmFPj0g47naFlEUTTaIx1l_HhtGEAIG7vuMD7qqYg-fj2DpPf7n1OWfzrRWwPnZToUZiJBIgfz-iorQ">
        <button v-if="active" v-on:click="buttonFetch">,      </button>
        <ul v-else class="personContent">
            <li>: {{ users[0].name }} </li>
            <li>: {{ users[0].username }} </li>
            <li>: {{ users[0].phone }} </li>
            <li>: {{ users[0].email }} </li>
        </ul>

        <div class="adBitcoin">
            <h1>Bitcoin Price Index</h1>
            <div>
                <p>    {{info.data.bpi.EUR.rate}} {{info.data.bpi.EUR.code}}</p>
            </div>
            <div class="adLinkBlock"></div>
        </div>
        <router-link to="/"></router-link>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data() {
        return{
            title: 'title',
            urlUser: 'https://jsonplaceholder.typicode.com/users',
            users: [],
            active: true,
            info: [],
            bpi: 'bpi'

        } 
    },
    methods: {
        async buttonFetch() {
            const response = await fetch(this.urlUser)
            const result = await response.json()
            this.users.push(...result)
            console.log(this.users);
            this.active = false
            console.log(this.info);
        }
        
    },
    mounted() {
        axios
            .get('https://api.coindesk.com/v1/bpi/currentprice.json')
            .then(response => (this.info = response));
    },
    
}
</script>

<style scoped>
    #appPerson{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    img{
        border-radius: 15px;
    }

    button{
        margin-top: 20px;
        border: 1px solid brown;
        padding: 12px 24px;
        border-radius: 15px;
        transition: .4s ease;
    }
    button:hover{
        cursor: pointer;
        background-color: brown;
    }

    ul{
        list-style: circle;
    }
    li{
        margin-bottom: 5px;
    }

    .adBitcoin{
        width: 400px;
        height: 260px;
        background-image: url('https://img5.goodfon.ru/wallpaper/nbig/2/b0/bitcoin-btc-orange-bitkoin-fon-fon.jpg');
        background-size: cover;
        background-position: center;
        margin-top: 25px;
        border: 1px solid black;
        border-radius: 15px;
        color: rgb(38, 0, 255);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        position: relative;
        opacity: 0.4;
        transition: .4s ease;
        

    }

    .adBitcoin:hover{
        opacity: 0.6;
    }

    .adBitcoin h1{
        font-size: 40px;
        margin: 0;
    }

    .adLinkBlock{
        position: absolute;
        top: 20px;
        right: 20px;
        font-size: 12px;
        background-color: rgba(0, 0, 0, 0.3);
        border-radius: 5px;
        padding: 5px 10px;
    }
</style>